@import url('https://fonts.googleapis.com/css2?family=Audiowide&display=swap');

@keyframes rotate-y {
  50% {
    transform: rotateY(15deg) rotateX(-6deg);
  }
}

.home {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: conic-gradient(at -10% 50%, deeppink, cyan);

  @media (prefers-color-scheme: dark) {
    background: conic-gradient(at -10% 50%, #212529, 50%, #495057, #212529);
  }

  &__spotify {
    position: absolute;
    top: 40px;
    left: 40px;

    @media (max-width: 767.98px) {
      top: 60px;
      left: auto;
    }
  }

  &__login_status {
    position: absolute;
    top: 40px;
    right: 40px;
    font-family: 'Audiowide', cursive;
    font-size: 10px;

    @media (prefers-color-scheme: dark) {
      color: white;
    }

    @media (max-width: 767.98px) {
      top: 20px;
      right: 20px;
    }
  }

  &__menu_wrapper {
    perspective: 30vw;
  }

  &__menu {
    --distance: 1px;
    --theme: hsl(180 100% 50%);
    --theme-bg: hsl(180 100% 50% / 25%);
    --theme-bg-hover: hsl(180 100% 50% / 40%);
    --theme-text: white;
    --theme-shadow: hsl(180 100% 10% / 25%);
    --_max-rotateY: 10deg;
    --_max-rotateX: 15deg;
    --_btn-bg: var(--theme-bg);
    --_btn-bg-hover: var(--theme-bg-hover);
    --_btn-text: var(--theme-text);
    --_btn-text-shadow: var(--theme-shadow);
    --_bounce-ease: cubic-bezier(0.5, 1.75, 0.75, 1.25);

    @media (prefers-color-scheme: dark) {
      --theme: hsl(255 53% 50%);
      --theme-bg: hsl(255 53% 71% / 25%);
      --theme-bg-hover: hsl(255 53% 50% / 40%);
      --theme-shadow: hsl(255 53% 10% / 25%);
    }

    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 2.5vh;
    will-change: transform;
    transition: transform 0.1s ease;
    animation: rotate-y 5s ease-in-out infinite;
    transform-style: preserve-3d;
    transform: rotateY(
        clamp(calc(var(--_max-rotateY) * -1), var(--y), var(--_max-rotateY))
      )
      rotateX(
        clamp(calc(var(--_max-rotateX) * -1), var(--x), var(--_max-rotateX))
      );

    > li {
      display: inline-flex;
      position: relative;
      transform-style: preserve-3d;
    }

    button {
      appearance: none;
      outline: none;
      border: none;
      background-color: var(--_btn-bg);
      color: var(--_btn-text);
      text-shadow: 0 1px 1px var(--_btn-text-shadow);
      font-size: 5vmin;
      font-family: 'Audiowide', cursive;
      padding-block: 0.75ch;
      padding-inline: 2ch;
      border-radius: 5px 20px;
      transform: translateZ(var(--distance));
      transform-style: preserve-3d;
      will-change: transform;
      transition: transform 0.2s ease, background-color 0.5s ease;
      cursor: pointer;

      &::after,
      &::before {
        content: '';
        opacity: 0.8;
        position: absolute;
        inset: 0;
        border: 1px solid var(--theme);
        border-radius: 5px 20px;
        transition: transform 0.1s ease-out;
      }

      &::after {
        transform: translateZ(calc(var(--distance) / 3));
        transition-duration: 0.5s;
      }

      &::before {
        border-width: 3px;
        transform: translateZ(calc(var(--distance) / 3 * -1));
        transition-duration: 0.3s;

        @media (prefers-color-scheme: dark) {
          box-shadow: 0 0 25px var(--theme), inset 0 0 25px var(--theme);
        }
      }

      &:is(:hover, :focus-visible):not(:active) {
        --distance: 15px;
        --distance: 3vmax;

        background-color: var(--_btn-bg-hover);
        transition-timing-function: var(--_bounce-ease);
        transition-duration: 0.4s;

        &::after {
          transition-duration: 0.5s;
        }

        &::before {
          transition-duration: 0.3s;
        }
      }
    }
  }

  &__footer {
    position: absolute;
    left: 40px;
    bottom: 40px;
    font-family: 'Audiowide', cursive;
    font-size: 10px;

    @media (prefers-color-scheme: dark) {
      color: white;
    }

    @media (max-width: 767.98px) {
      left: 20px;
      bottom: 20px;
    }
  }

  &__metamask_donate {
    position: absolute;
    right: 40px;
    bottom: 40px;

    @media (max-width: 767.98px) {
      right: 20px;
      bottom: 20px;
    }
  }
}
